export default "import Button from \"component/Button\";\nimport stlyes from './index.less'\nconst ContainerButtonDemo = (props: any) => {\n\n\n    return (<div>\n        <div className={stlyes.content}>\n            <Button type=\"contained\" >\n                默认按钮\n            </Button>\n            <Button type=\"contained\" primary>\n                主要的按钮\n            </Button>\n            <Button\n                type=\"contained\"\n                danger\n            >危险的按钮 </Button>\n\n            <Button disabled type=\"contained\">禁用按钮</Button>\n            <Button loading type=\"contained\">加载中的按钮</Button>\n            <Button loading primary type=\"contained\">加载中的按钮</Button>\n        </div>\n<div className={stlyes.content}>\n    <Button primary size=\"s\">小按钮</Button>\n    <Button primary size=\"m\">中按钮</Button>\n    <Button primary size=\"l\">大按钮</Button>\n    <Button primary size=\"xl\">特大按钮</Button>\n</div>\n\n<div className={stlyes.content}>\n    <Button type=\"contained\" primary style={{ width: '100%'}}>整行按钮</Button>\n</div>\n\n    </div>)\n}\n\nexport default ContainerButtonDemo;";